* {
  box-sizing: border-box;
}

.stage {
  padding: 50px;
  display: grid;
  gap: 50px;
}

.box {
  background: black;
  color: white;
  font-size: 24px;  
  display: none;

  &.is-open {
    display: block;
  }

  &--transition {
    transition: all 0.7s ease-out;
    opacity: 0;

    &.is-opening {
      opacity: 1;
    }  
  }

  &--animation {
    opacity: 0;

    &.is-opening {
      animation: fade-in 0.7s ease-out forwards;
    }

    &.is-closing {
      animation: fade-out 0.7s ease-out;
    }
  }

  &__content {
    padding: 20px;
  }
}

@keyframes fade-in {
  from { opacity: 0 }
  to { opacity: 1 }
}

@keyframes fade-out {
  from { opacity: 1 }
  to { opacity: 0 }
}
